import React, { Fragment } from 'react';
import classes from './Content.module.css'
import { FcFeedback, FcAbout } from 'react-icons/fc';
import { RiPriceTag3Line, RiFileList2Line } from 'react-icons/ri';
import { GrUserNew } from 'react-icons/gr';
import { AiOutlineLogin, AiOutlineContacts, AiOutlineCustomerService, AiOutlineHome, AiOutlineUser } from 'react-icons/ai';
import { Switch, Route, useLocation } from "react-router-dom";
import ChildNavs from '../ChildNavs/ChildNavs';

function useQuery() {
    debugger
    //?pagenumber=10&sorting=firstname
    return new URLSearchParams(useLocation().search)
}

const Content = (props) => {
    const QueryParams = useQuery();

    return (
        <Fragment>
            <div className={classes.Content}>
                <Switch>
                    <Route exact path="/">
                        <h2 className={classes.Icon}><AiOutlineHome /></h2>
                    </Route>
                    <Route path="/features">
                        <h2 className={classes.Icon}><RiFileList2Line /></h2><ChildNavs />
                    </Route>
                    <Route path="/pricing">
                        <h2 className={classes.Icon}><RiPriceTag3Line /></h2>
                    </Route>
                    <Route path="/about-us">
                        <h2 className={classes.Icon}><FcAbout /></h2>
                    </Route>
                    <Route path="/contact-us">
                        <h2 className={classes.Icon}><AiOutlineContacts /></h2>
                    </Route>
                    <Route path="/customer-service">
                        <h2 className={classes.Icon}><AiOutlineCustomerService /></h2>
                    </Route>
                    <Route path="/feedback">
                        <h2 className={classes.Icon}><FcFeedback /></h2>
                    </Route>
                    <Route path="/login">
                        <h2 className={classes.Icon}><AiOutlineLogin /></h2>
                    </Route>
                    <Route path="/register">
                        <h2 className={classes.Icon}><GrUserNew /></h2>
                    </Route>
                    <Route path="/users">
                        <div className='text-center mb-5'>
                            <h1 className='mb-4'><AiOutlineUser /></h1>
                            <h5>sort : &nbsp;<strong style={{ textTransform: "uppercase" }}>{QueryParams.get('sort')} </strong></h5>
                            <h5>pageno : &nbsp;<strong >{QueryParams.get('pageno')}</strong></h5>
                            <h5>pagesize : &nbsp;<strong >{QueryParams.get('pagesize')}</strong></h5>
                        </div>
                    </Route>
                    <Route path='*'>Not found</Route>
                </Switch>
                <p className='px-5 pb-5'>"Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum."</p>
            </div>
        </Fragment>
    );
};

export default Content;