import React, { Fragment } from 'react';
import classes from './Header.module.css';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { Link, withRouter } from "react-router-dom";

const Header = (props) => {
    console.log(props)
    return (
        <Fragment>
            <header className={classes.Header}>
                <Navbar collapseOnSelect expand="lg" variant="dark">
                    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                    <Navbar.Collapse id="responsive-navbar-nav">
                        <Nav className="mr-auto">
                            <Link to={{ pathname: props.match.url }}>Home</Link>
                            <Link to={{ pathname: props.match.url + 'users' }}>Users</Link>
                            <Link className='mr-3' to="/features">Features</Link>
                            <Link className='mr-3' to="/pricing">Pricing</Link>
                            <Link className='mr-3' to="/feedback">Feedback</Link>
                        </Nav>
                        <Nav>
                            <Link className='mr-3' to="/login">LogIn</Link>
                            <Link className='mr-3' to="/register">Register</Link>
                        </Nav>
                    </Navbar.Collapse>
                </Navbar>
            </header>
        </Fragment>
    );
};

export default withRouter(Header);